import { useEffect, useState } from "react"
import { TextField, TextFieldProps } from "@mui/material"
import { useDeviceInfo } from "@rp/react/web/common/hooks"

const ModalTextfield = (props: TextFieldProps) => {
  const { isIOS, isMobile } = useDeviceInfo()
  const { autoFocus, ...forwarded } = props
  const hasAutoFocus = autoFocus && !isIOS && !isMobile
  const [ref, setRef] = useState<HTMLInputElement | null>(null)
  useEffect(() => {
    if (hasAutoFocus && ref) ref.focus()
  }, [hasAutoFocus, ref])

  return (
    <TextField
      {...forwarded}
      variant="outlined"
      margin="dense"
      autoFocus={hasAutoFocus}
      inputRef={setRef}
    />
  )
}

export { ModalTextfield }
